@import url(https://cdn.jsdelivr.net/gh/aymanbagabas/iosevka-fonts@v6.1.2/dist/iosevka/iosevka.min.css);
@import url(https://cdn.jsdelivr.net/npm/victormono@latest/dist/index.min.css);
@import url(https://cdn.jsdelivr.net/gh/devicons/devicon@v2.14.0/devicon.min.css);
@import url(https://cdnjs.cloudflare.com/ajax/libs/animate.css/4.1.1/animate.min.css);
@keyframes floating{0%{transform:translateY(0)}50%{transform:translateY(-10px)}100%{transform:translateY(0)}}@keyframes scale-in{0%{transform:scale(0);opacity:1}100%{transform:scale(1);opacity:1}}*{box-sizing:border-box;scroll-behavior:smooth}html,body{background-color:#1d2021;font-family:"Iosevka",sans-serif}a:hover{transition:.3s;color:#689d6a}.app{height:100%;margin:0 auto}.button{border:unset;color:#fbf1c7;background-color:#292d32 !important;filter:drop-shadow(5px 5px 0px rgba(41, 45, 50, 0.6)) !important;margin:0px 8px 0px 8px}.button:hover{color:#fbf1c7;transform:translateY(-0.5em);transition:transform .3s ease}.content{text-align:left;font-size:20px;width:100%}.content h1,.content h2,.content h3,.content h4,.content h5,.content h6{color:#fbf1c7}.content .description{color:#fbf1c7;font-family:"Victor Mono",monospace;font-style:italic}.card{color:#fbf1c7;background-color:#1d2021;font-family:-apple-system,BlinkMacSystemFont,Segoe UI,Helvetica,Arial,sans-serif,Apple Color Emoji,Segoe UI Emoji;animation:scale-in .7s cubic-bezier(0.25, 0.46, 0.45, 0.94) both}.card .media-content{font-weight:bold}.card .c-content{font-size:14px}.navbar{background-color:#282828}.navbar .navbar-brand .navbar-burger,.navbar .navbar-item{color:#fbf1c7}.navbar .navbar-menu{background-color:#1d2021}.navbar .navbar-end .navbar-item{background:none;margin:0px 24px 0px 24px}.navbar .navbar-end .navbar-item:hover{transition:.3s;color:#689d6a}.footer{color:#fbf1c7;background-color:#1d2021;margin-top:3em}.footer p{font-size:16px}.about-container,.projects-container{padding:6rem;height:100% !important}.about-container .introduction{display:flex;flex-direction:column;justify-content:center;align-items:center}.about-container .introduction .introduction section{flex-direction:unset}.about-container .introduction .image-title{border-radius:100%;filter:drop-shadow(0px 0px 0px rgba(41, 45, 50, 0.8));height:16em !important;width:16em !important;max-width:none !important;animation:3s ease-in-out infinite;animation-name:floating}.projects-container{background-color:#282828;transform:skewY(-5deg)}.projects-container .projects-list{display:flex;flex-direction:column;justify-content:center;align-items:center;transform:skewY(5deg)}.projects-container .projects-list h1{color:#fbf1c7;font-size:36px;font-weight:bold;align-items:center;margin-bottom:1em}
